@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Merriweather', sans-serif;
}
.App{
    height: 100vh;
    width: 100%;
    position: absolute;
}

.temp-box{
    min-height: 450px;
    max-width: 320px;
    background: linear-gradient(rgb(164, 166, 219),rgb(171, 181, 228));
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 15px;
    text-align: center;
    padding: 30px 20px;
    overflow: hidden;
}

.inputField{
    max-width: 75%;
    padding: 10px ;
    font-size: 18px;
    border: none;
    outline: none;
    border-radius: 20px;
}

.info{
    position: relative;
    bottom: 10px;
}

h1, h2, h3{
    padding: 3px;
}
h1{
    font-size: 2.2em;
}
h2{
    font-size: 1.4em;
}

h3{
    font-size: 0.9em;
    font-weight: 300;
    margin-top: 10px;
}


.cloud{
    display: none;
}
.fa-cloud{
    font-size: 60px;
    color: #efefef;
}

.fa-cloud-sun-rain{
    font-size: 60px;
    -webkit-animation: locIcon 10s infinite;
            animation: locIcon 10s infinite;
}
@-webkit-keyframes locIcon{
    0%{
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }
    50%{
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
    100%{
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

}
@keyframes locIcon{
    0%{
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }
    50%{
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
    100%{
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

}

.cloud_one,.cloud_two,.cloud_three{
    display: inline-block;
    position: relative;
}
.cloud_one{
    top: 20px;
    -webkit-animation: cloud1 80s infinite;
            animation: cloud1 80s infinite;
}
@-webkit-keyframes cloud1{
    0%{
        left: -350px;
    }
    100%{
        left: 250px;
    }
}
@keyframes cloud1{
    0%{
        left: -350px;
    }
    100%{
        left: 250px;
    }
}
